  font-size: 66px;
  color: #43464B;
  margin-top: 85px;
  margin-bottom: 10px;
  text-align: center;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

}

 .App {
   text-align: center;
   width: 600px;
   height: 100%;
   position: absolute;
   top: 50%;
   left: 50%;
   -webkit-transform: translate(-50%, -50%);
       -ms-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
   background: #f8faf5;
 }



